<template>
	<div class="wrapper">
		<!-- header -->
		<header>
			<p></p>
		</header>

		<!--  -->
		<div class="credit">
			<div class="credit-icon"><i class="fa fa-gift"></i></div>
			<h2>{{ credit }}</h2>
			<p></p>
			<div class="credit-use">
				<button @click="toIndex"></button>
			</div>
		</div>
		<!--  -->
		<h3>
			
			<span class="credit-rule" @click="showRuleModal">
				
				<i class="fa fa-question-circle"></i>
			</span>
		</h3>

		<!--  -->
		<ul class="credit-detailed">
			<ul class="credit-detailed">
				<li v-for="item in detailArr" :key="item.id">
					<div class="credit-detailed-left" v-if="item.points > 0">
						<p></p>
						<p>{{ item.integrationDate }}</p>
					</div>
					<div class="credit-detailed-left" v-if="item.points < 0 && item.integrationState==1">
						<p></p>
					</div>

					<div class="credit-detailed-left" v-if="item.points < 0 && item.integrationState==2">
						<p></p>
						<p>{{ item.integrationDate }}</p>
					</div>
					<div class="credit-detailed-right">
						<p :style="{color: item.points > 0 ? 'green' : 'red'}"><span v-if="item.points > 0">+</span>{{ item.points }}</p>

					</div>
				</li>
			</ul>
		</ul>

		<!--  -->
		<Footer></Footer>
	</div>

	<!--  -->
	<div v-if="isRuleModalVisible" class="rule-modal-overlay" @click="closeRuleModal">
		<div class="rule-modal-content" @click.stop>
			<h4></h4>
			<p class="rule-modal-content-title">1.</p>
			<p>&nbsp;&nbsp;&nbsp;100100<br></p>
			<p class="rule-modal-content-title">2.</p>
			<p>&nbsp;&nbsp;&nbsp;1000100
				
				<br>
				24.5252.5</p>
			<p class="rule-modal-content-title">3.</p>
			<p>&nbsp;&nbsp;&nbsp;202391202491<br></p>
			<p class="rule-modal-content-title">4.</p>
			<p>&nbsp;&nbsp;&nbsp; -> </p>
			<p class="rule-modal-content-title">5.</p>
			<p>&nbsp;&nbsp;&nbsp;</p>
			<p class="rule-modal-content-bottom">&nbsp;&nbsp;&nbsp;</p>
		</div>

	</div>

</template>

<script>
	import {
		ref,
		inject,
		onMounted,
		watch,
		computed,
		onUnmounted
	} from 'vue';
	import {
		useRouter
	} from 'vue-router';
	import axios from 'axios';
	import qs from 'qs';
	import Footer from '../components/Footer.vue';
	export default {
		name: 'Credit',
		components: {
			Footer
		},
		setup() {
			const detailArr = ref([]);
			const user = ref(JSON.parse(sessionStorage.getItem('user')));
			const isRuleModalVisible = ref(false);
			const credit = ref(0);
			const router = useRouter();

			const toIndex = () => {
				router.push('/index');
			};
			const showRuleModal = () => {
				isRuleModalVisible.value = true;
			};
			const closeRuleModal = () => {
				isRuleModalVisible.value = false;
			};

			// 
			const getCreditByUserId = () => {
				axios.get(`/users/${user.value.userId}/integrations/credit`)
					.then(response => {
						if (response.data == '') {
							credit.value = 0;
						} else {
							credit.value = response.data;
						}
						// console.log(':', credit.value);
					})
					.catch(error => {
						console.error(error);
					});
			};

			// 
			const getDetailsByUserId = () => {
				axios.post('IntegrationController/getDetailsByUserId', qs.stringify({
						userId: user.value.userId,
					}))
					.then(response => {
						detailArr.value = response.data;
						// console.log(':', detailArr.value);
					})
					.catch(error => {
						console.error(error);
					});
			};

			onMounted(() => {
				getCreditByUserId();
				getDetailsByUserId();
			});


			return {
				detailArr,
				user,
				isRuleModalVisible,
				credit,
				toIndex,
				showRuleModal,
				closeRuleModal
			}
		}
	}
</script>

<style scoped>
	/******************  ****************/
	.wrapper {
		width: 100%;
		height: 100%;
		background-color: #F5F4F6;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		padding-bottom: 85vw;
	}

	/****************** header ****************/
	.wrapper header {
		width: 100%;
		height: 20vw;
		background-color: #0097ff;
		color: #fff;
		font-size: 4.8vw;
		position: fixed;
		left: 0;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	/******************  ****************/
	.wrapper .credit {
		width: 90%;
		height: 80vw;
		margin: 15vw auto 5vw;
		background-color: #fff;
		border-radius: 2vw;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		align-items: center;
		z-index: 2;
	}

	.wrapper .credit-icon {
		margin: 15vw 10px 10px 10px;
	}

	.wrapper .credit i {
		font-size: 9vw;
		color: mediumpurple
	}

	.wrapper .credit h2 {
		font-size: 8vw;
		margin-bottom: 1vw;
	}

	.wrapper .credit p {
		font-size: 3vw;
		color: #777;
		margin-bottom: 3vw;
	}

	.wrapper .credit .credit-use {
		width: 100%;
		box-sizing: border-box;
		padding: 5vw 10vw 10vw 10vw;
	}

	.wrapper .credit .credit-use button {
		width: 100%;
		height: 13vw;
		font-size: 5vw;
		font-weight: 700;
		color: #fff;
		background-color: #0097ff;
		border-radius: 50px;
		border: none;
		outline: none;
	}

	/******************  ****************/
	.wrapper .credit-detailed {
		width: 100%;
		z-index: 2;
	}

	.wrapper .credit-detailed::after {
		content: "";
		display: block;
		height: 14vw;
	}

	.wrapper h3 {
		margin-left: 6vw;
		font-size: 4vw;
	}

	.wrapper .credit-detailed li {
		width: 90%;
		height: 15vw;
		background-color: #fff;
		margin: 2vw auto;
		border-radius: 2vw;

		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.wrapper .credit-detailed li .credit-detailed-left {
		margin-left: 4vw;
	}

	.wrapper .credit-detailed li .credit-detailed-left p:first-child {
		font-size: 3.8vw;
		color: #111;
	}

	.wrapper .credit-detailed li .credit-detailed-left p:last-child {
		font-size: 3vw;
		color: #777;
		margin-top: 1vw;
	}

	.wrapper .credit-detailed li .credit-detailed-right {
		margin-right: 4vw;
		margin-bottom: 5vw;
	}

	.wrapper .credit-detailed li .credit-detailed-right p {
		font-size: 3.5vw;
		color: red;
	}

	.credit-rule {
		font-size: 2.6vw;
		margin-left: 2vw;
		color: #5d5f60;
		cursor: pointer;
	}

	/******************  ****************/
	.credit-rule i {
		margin-left: 1vw;
		font-size: 2vw;
		color: grey;
	}

	.rule-modal-overlay {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, 0.4);
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 1000;
	}

	.rule-modal-content {
		background-color: #fff;
		padding: 4vw;
		border-radius: 2.5vw;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
		max-width: 85vw;
		width: 100%;
		position: relative;
		max-height: 70vh;
		overflow-y: auto;
	}


	.rule-modal-content h4 {
		font-size: 5vw;
		margin-bottom: 1.5vw;
		font-weight: 600;
		border-bottom: 2px solid #dedede;
		padding-bottom: 1vw;
		text-align: center;
		color: #333;
		font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
		border-image: linear-gradient(to right, #aaa, #333, #aaa);
		border-image-slice: 1;
	}



	.rule-modal-content p {
		font-size: 4vw;
		line-height: 5.5vw;
		color: #666;
		margin-bottom: 1.2vw;
	}


	.rule-modal-content-title {
		font-size: 4.5vw;
		color: black;
		font-weight: bold;
		margin-bottom: 1.5vw;
	}

	.rule-modal-content-bottom {
		font-size: 4.5vw;
		font-weight: 500;
		color: #444;
		margin-top: 2vw;
		padding-left: 1.5vw;
		border-top: 1px solid #dedede;
		padding-top: 1.5vw;
	}
</style>
